import React, { Component } from 'react';
import Meta from './Meta';
import Grid from './Grid';
import Header from './Header';
import styled, { injectGlobal, ThemeProvider } from 'styled-components';

const theme = {
  red: '#FF0000',
  black: '#393939',
  grey: '#3A3A3A',
  blue: 'blue',
  lightgrey: '#E1E1E1',
  offWhite: '#EDEDED',
  gradient: 'linear-gradient(60deg, rgba(2,0,36,1) 0%, rgba(96,19,102,1) 0%, rgba(185,37,165,0.9626225490196079) 48%, rgba(127,92,193,1) 64%, rgba(0,212,255,1) 99%)',
  maxWidth: '1000px',
  bs: '0 12px 24px 0 rgba(0, 0, 0, 0.09)'
};

const StyledPage = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
  background: ${props => them}
`;

injectGlobal`
  @font-face {
    font-family: 'radnika_next';
    src: url('/static/radnikanext-medium-webfont.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  html {
    box-sizing: border-box;
    font-size: 10px;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }
  body {
    padding: 0;
    margin: 0;
    font-size: 1.5rem;
    line-height: 2;
    font-family: 'radnika_next';
  }
  a {
    text-decoration: none;
    color: ${theme.black};
  }
  button {  font-family: 'radnika_next'; }
`;

class Page extends Component {
  render() {

    return (
      <ThemeProvider theme={theme}>
        <StyledPage>
          <Meta />
          <Header theme />
          <Grid/>
        </StyledPage>
      </ThemeProvider>
    )
  }
};

export default Page;